tify-content: center; \n  align-items: center; \n  font-size: 40px;\n\n}\n\n.tron__inner {\n  text-align: center;\n  margin: 0 auto;\n  max-width: 800px;\n}\n\n.tron__title {\n  margin-bottom: 10px;\n  color: #ffffff;\n  font-size: 80px;\n  font-weight: 900;\n}\n\n.tron__tagline {\n  margin-bottom: 20px;\n}\n\n\nmain {\n  width: 100%;\n  margin: 0 auto;\n}\n\n.content {\n  padding: 25px;\n  text-align: left;\n  overflow: auto;\n}\n\n.label {\n  width: 100%;\n  margin: 20px auto;\n  text-align: center;\n}\n.explore_label {\n  text-align: center;\n  font-size: 38px;\n  padding-top: 40px;\n}\n\n.contentlist {\n  margin: 20px;\n  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.3);\n  width: 95%;\n  border-radius: 10px;\n  overflow: hidden;\n  background-color: #f5f5f5;\n  transition: transform 0.3s ease-in-out;\n}\n\n.contentlist:hover {\n  transform: scale(1.02);\n}\n\n.contentlist a {\n  text-decoration: none;\n  color: #2c3e50;\n}\n\n.image {\n  max-height: 220px;\n  width: 100%;\n  border-top-right-radius: 10px;\n  border-top-left-radius: 10px;\n  object-fit: cover;\n}\n\n.restaurant_info {\n  padding: 24px;\n  color: #34495e;\n  font-weight: 400;\n}\n\n.info_title:hover {\n  color: #e67e22;\n}\n\n.info_city {\n  margin-top: 5px;\n  font-style: italic;\n  color: #7f8c8d;\n}\n\n.info_desc {\n  margin-top: 10px;\n  text-align: justify;\n  line-height: 1.5;\n  font-weight: 400;\n}\n\n.info_rates {\n  color: #ffffff;\n  text-align: center;\n  margin-top: 10px;\n  padding: 8px;\n  font-weight: bold;\n  border-radius: 20px;\n  background-color: #BBAB8C;\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\n}\n\n\n.detail {\n  width: 100%;\n  margin: 60px auto;\n  text-align: center;\n}\n\n\n.detail_head {\n  text-align: center;\n  color: #34495e;\n  font-size: 32px;\n  font-weight: bold;\n  text-transform: uppercase;\n  letter-spacing: 2px;\n}\n\n\n.detail_content {\n  padding: 25px;\n  overflow: auto;\n  background-color: #f5f5f5;\n  border-radius: 10px;\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\n}\n\n.contentdetail {\n  margin: auto;\n  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.3);\n  width: 80%;\n  border-radius: 15px;\n  overflow: hidden;\n  background-color: #FFFFFF;\n}\n\n.detail_title {\n  font-weight: 800;\n  margin-bottom: 20px;\n  text-align: center;\n  text-transform: uppercase;\n  letter-spacing: 1px;\n  color: #333333;\n  font-size: 20px;\n}\n\n.info_list {\n  text-align: left;\n  margin: 20px auto;\n  padding: 30px;\n  border: 1px solid #BBAB8C;\n  border-radius: 10px;\n  background-color: #f9f9f9;\n}\n\n.foods_detail,\n.drinks_detail {\n  display: flex;\n  flex-direction: column;\n  text-align: center;\n  align-content: center;\n  margin-bottom: 30px;\n  font-weight: 400;\n}\n\n.restaurant_info_detail {\n  padding: 25px;\n  color: #34495e;\n  font-weight: 400;\n}\n\n.restaurant_menu {\n  display: grid;\n  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));\n  gap: 30px;\n  margin: 30px auto;\n  padding: 30px;\n  border: 2px solid #BBAB8C;\n  border-radius: 10px;\n  background-color: #f9f9f9;\n}\n\n.image_detail {\n  width: 100%;\n  max-height: 500px;\n  border-radius: 10px;\n  object-fit: cover;\n}\n\n.list {\n  margin-bottom: 12px;\n  font-size: 18px;\n  color: #2c3e50;\n}\n\n.restaurant_review {\n  text-align: center;\n  margin-top: 40px;\n}\n\n.review_header {\n  margin-top: 15px;\n  font-size: 24px;\n  font-weight: bold;\n  color: #34495e;\n}\n\nul {\n  list-style-type: none;\n  padding: 0;\n}\n.favorite{\n  text-align: center;\n  font-size: 38px;\n  padding-top: 40px;\n  \n}\n\n.favoritehead{\n  padding-top: 40px;\n  text-align: center;\n  color: #333333;\n}\n\n.like {\n  width: 55px;\n  height: 55px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  background-color: #db0000;\n\n  position: fixed;\n  bottom: 16px;\n  right: 16px;\n  border-radius: 50%;\n  border: 0;\n\n  font-size: 18px;\n  color: white;\n  cursor: pointer;\n}\n\n.review_header {\n  margin-bottom: 5px;\n}\n\n.footer {\n  background-color: #EBE3D5;\n  padding: 40px;\n  width: 100%;\n  text-align: center;\n  font-size: 24px;\n  font-weight: 300;\n  bottom: 0;\n}\n\n @media  screen and ( max-width: 1050px){\n    .name{\n      font-size: 26px;\n    }\n    .nav_list{\n      width: 30%;\n      font-size: 13px;\n    }\n }\n  \n @media  screen and ( max-width: 500px){\n  .header{\n    display: grid;\n    grid-template-columns: 1fr 1fr;\n    padding: 12px 25px;\n    margin-top: 0;\n  }\n  .nav-mob{\n    display: flex;\n    align-items: center;\n    justify-content: flex-start;\n    margin-top: 15px;\n    list-style-type: none;\n  }\n\n  .name{\n    display: flex;\n    justify-content: flex-end;\n    align-items: center;\n    font-size: 24px;;\n  }\n  #hamburger{\n    line-height: 44px;\n    margin-top: 0px;\n    color:black;\n    text-decoration: none;\n    background-color: transparent;\n    border: none;\n  }\n  .nav {\n    z-index: 10;\n    background-color: #EBE3D5;\n    width: 150px;\n    position: absolute;\n    padding: 8px;\n    -webkit-transform: translate(100%, 0);\n    transform: translate(-100%, 0);\n    transition: transform 0.3s ease;\n    top: 92px;\n    overflow-x: hidden;\n  }\n  .nav_list{\n    display: list-item;\n    border-bottom: 1px solid #EBE3D5;\n    width: 100%;\n    text-align: left;\n    flex-direction: column;\n    align-items: flex-start;\n\n  }\n  .open {\n    -webkit-transform: translate(0, 0);\n    transform: translate(0, 0);\n  }\n  \n\n}\n@media screen and (min-width: 500px){\n  .Menu{\n    display: none;\n  }\n}\n\n@media screen and (min-width: 650px) {\n  .header{\n    grid-template-columns: 1fr auto;\n    padding: 8px 32px;\n  }\n  .nav{\n    position: static;\n    width: 100%;\n  }\n  .maincontent {\n    display: grid;\n    grid-template-columns: 1fr 1fr;\n    grid-column-gap: 10px;\n    grid-row-gap: 15px;\n  }\n  .Menu{\n    display: none;\n  }\n}\n\n@media screen and (min-width: 950px) {\n  .content {\n    display: grid;\n    grid-template-columns: 1fr 1fr 1fr;\n  }\n}\n@media screen and (min-width: 1200px) {\n  .jumbotron {\n    min-width: 1000px;\n  }\n  main {\n    max-width: 1200px;\n  }\n  .Menu{\n    display: none;\n  }\n  \n}\n\n \n"],"sourceRoot":""}]);
